<template>
    <div>
        <el-card class="box-card">
            <span>居民管理</span>
        </el-card>
        <el-row>
            <el-col :span="24">
                <el-form ref="form" :model="query" label-width="80px">
                    <el-form-item label="楼栋ID：">
                        <el-cascader
                        v-model="query.buildingid"
                        placeholder="请选择"
                        :options="this.$store.state.building.buildingdata"
                        :props="{label:'name',value:'id',checkStrictly: true }"
                        filterable></el-cascader>
                    </el-form-item>
                    <el-form-item label="姓名：">
                        <el-input v-model="query.name" placeholder="居民姓名"></el-input>
                    </el-form-item>
                    <el-form-item label="电话">
                        <el-input v-model="query.name" placeholder="居民电话"></el-input>
                    </el-form-item>
                    <el-form-item label="房间号">
                        <el-input v-model="query.name" placeholder="房间号"></el-input>
                    </el-form-item>
                    <el-form-item label="用户类型">
                        <el-select v-model="query.region" placeholder="请选择">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                     <el-form-item>
                        <el-button type="primary">查询</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
            <el-row class="butt">
                <el-button type="primary" @click="dialogVisible = true">添加</el-button>  
                <el-button type="danger" @click="del">批量删除</el-button>
                <el-button type="success">批量审核</el-button>
            </el-row>

            <!-- 表格 -->
          <Table
          :loading="loading"
          :tableData="tableData"
          :tableCouml="tableCouml"
          @idshow="idshow">

          <template #imgage="data">
            <el-image
            style="width: 50px; height: 50px"
            :src="img+data.data.photo"
            fit="fill"></el-image>
          </template>
          <template #sex="data">
            <p>{{data.data.sex==1?'男':'女'}}</p>
          </template>
          <template #state="data">
            <p>{{data.data.state==2?'已审核':'未审核'}}</p>
          </template>

          <template #option="data">
            <el-button size="mini" type="success" @click="mod(data.data)">修改</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(data.data)">删除</el-button>
          </template>
    </Table>

    <!-- 添加 -->
            <Userrinfotian :dialogVisible="dialogVisible" @change="change" :modcom="modcom"></Userrinfotian>
    </div>
</template>

<script>
import Table from "@/components/commts/Table.vue";
import Userrinfotian from '@/components/userinfo/Userinfotian.vue'
export default {
        data(){
            return{
                img:'http://estate.eshareedu.cn/estate/upload/',
                loading: true,
                query:{
                    page:1,
                    psize:5
                },
                tableData: [],
                tableCouml: [
                            { prop: "id", label: "ID", width: "60"},
                            { prop: "photo", label: "头像",width: "78",isshow: true, showname: "imgage" },
                            { prop: "name", label: "姓名",width: "78",},
                            { prop: "usertypeName", label: "业主类型",width: "78"},
                            { prop: "sex", label: "性别",width: "65",isshow: true, showname: "sex"},
                            { prop: "cardid", label: "身份证号",width: "100"},
                            { prop: "mobile", label: "手机号",width: "85"},
                            { prop: "usernative", label: "籍贯",width: "85"},
                            { prop: "nation", label: "民族",width: "65"},
                            { prop: "building", label: "小区",width: "100"},
                            { prop: "houseno", label: "房间号",width: "78"},
                            { prop: "state", label: "审核状态",width: "92",isshow: true, showname: "state"},
                            { label: "操作", isshow: true, showname: "option",},
                        ],
                total:0,
                ids:[],
                dialogVisible: false,
                modcom:{}
            }
        },
        components:{
            Table,
            Userrinfotian
        },
        created(){
            this.getimet()
        },
        methods:{
            // 修改
            mod(val){
                this.modcom = JSON.parse(JSON.stringify(val))
                this.dialogVisible = true
            },
            change(val){
                this.dialogVisible = val
                this.getimet()
            },
            // 删除
            handleDelete(data){
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async () => {
                    let res = await this.http.get('/userinfo/delete',{id:data.id})
                    if(res.errCode == '10000'){
                        this.$message.success('删除成功')
                        this.getimet()
                    }
                }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: '已取消删除'
                    });
                });
            },
            // 批量删除
            idshow(val){
                this.ids.push(val)
                // console.log(val)
            },
             del(){
                if(this.ids.length == 0){
                    this.$message.error('请选择要删除的记录')
                    return
                }
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async () => {
                    let res = await this.http.post('/userinfo/deleteall',{ids:this.ids})
                    if(res.errCode == '10000'){
                        this.$message.success('删除成功')
                        this.getimet()
                    }
                }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: '已取消删除'
                    });
                });
            },
             // 列表
            async getimet(){
                let res = await this.http.get('/userinfo/list',this.query)
                // console.log(res)
                if(res.errCode == '10000'){
                this.tableData = res.data.list
                this.total = res.data.counts
                this.loading = false
                }
            },
        }
}
</script>

<style lang="less" scoped>
@import '@/assets/css/admin.css';
</style>