<template>
    <el-dialog
    :title="title"
    :visible.sync="dialogVisible"
    width="65%"
    center="center"
    :before-close="handleClose">
        <el-form ref="form" :model="form" label-width="80px">
                <div>
                    <el-form-item label="头像" class="ol">
                        <el-upload
                        :headers="headers"
                        class="avatar-uploader"
                        action="http://estate.eshareedu.cn/estate/api/upload/add"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="imageUrl" :src="this.title == '修改居民'?imageUrl+this.form.photo:imageUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="楼栋" style="width:100%">
                            <el-cascader
                            placeholder="请选择"
                            v-model="form.l"
                            :options="this.$store.state.building.buildingdata"
                            :props="{ label:'name',value:'id',checkStrictly: true }"
                            clearable></el-cascader>
                    </el-form-item>
                    <el-form-item label="房间号" style="width:100%">
                        <el-select v-model="form.houseid" placeholder="请选择">
                            <el-option v-for="(imet,index) in list" :key="index" :label="imet.name" :value="imet.id"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="姓名" style="width:80%">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="性别" >
                        <el-radio v-model="form.sex" label="1">男</el-radio>
                        <el-radio v-model="form.sex" label="0">女</el-radio>
                    </el-form-item>
                </div>
                <div class="op">
                     <el-form-item label="民族" style="width:80%">
                        <el-input v-model="form.nation"></el-input>
                    </el-form-item>
                    <el-form-item label="籍贯" style="width:80%">
                        <el-input v-model="form.usernative"></el-input>
                    </el-form-item>
                    <el-form-item label="身份证号" style="width:80%">
                        <el-input v-model="form.cardid"></el-input>
                    </el-form-item>
                    <el-form-item label="联系电话" style="width:80%">
                        <el-input v-model="form.mobile"></el-input>
                    </el-form-item>
                    <el-form-item label="住户类型" style="width:100%">
                        <el-select v-model="form.usertype" placeholder="请选择">
                            <el-option v-for="(imet,index) in type" :key="index" :label="imet.name" :value="imet.id"></el-option>
                        </el-select>
                    </el-form-item>
                </div>
        </el-form>
    <span slot="footer" class="dialog-footer">
        <el-button @click="serate">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
    </span>
    </el-dialog>
</template>

<script>
export default {
    props:{
        dialogVisible:{
            type:Boolean,
            default:()=>false
        },
        modcom:{
            type:Object,
            default:()=>{}
        }
    },
    data(){
        return{
            headers:{Authorization:sessionStorage.getItem('token')},
            form:{},
            imageUrl: '',
            query:{
                page:1,
                psize:5000
            },
            list:[],
            type:[],
            title:'添加居民'
        }
    },
    created(){
        this.getid()
        // this.seratch()
        this.getusertype()
    },
    updated(){
        this.title = this.modcom.id?'修改居民':'添加居民'
        this.form = this.modcom?this.modcom:{}

        if(this.title == '修改居民'){
            return this.imageUrl = "http://estate.eshareedu.cn/estate/upload/"
        }else{
            return this.imageUrl = ""
        }
    },
    methods:{
        //居民类型
        async getusertype(){
            let res = await this.http.get('/userinfotype/list',this.query)
            // console.log(res)
            if(res.errCode == 10000){
                this.type = res.data.list
            }
        },
        // 房间号
       async getid(){
            let res = await this.http.get('/houses/list',this.query)
            // console.log(res)
            if(res.errCode == 10000){
                this.list = res.data.list
            }
        },
        // 确定
        async submit(){
            let res = await this.http.post('/userinfo/add',this.form)
            // console.log(res)
            if(res.errCode == 10000){
                this.$message.success('登录成功')
                this.$emit('change',false)
                this.form = {}
            }
            // console.log(this.form)
        },
        // 取消
        serate(){
            this.$emit('change',false)
        },
          handleClose(done) {
            this.$confirm('确认关闭？')
            .then(() => {
                // done();
                this.$emit('change',false)
            })
            .catch(() => {});
      },
      handleAvatarSuccess(res, file) {
        this.form.photo = res.data
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      }
    }
}
</script>

<style lang="less" scoped>
.el-form{
    display: flex;
    div{
        flex: 1;
        .ol{
            /deep/.el-form-item__label{
            margin-top: 30px !important;
            margin-right: 30px !important; 
        }
        
        }
        /deep/.el-input__inner{
            border: 1px solid #ccc !important;
        }
    }
    .op{
        margin-left: 90px;
    }
}
 .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
/deep/.el-upload{
    border: 1px dashed #ddd !important;
    border-radius: 6px !important;
}
</style>